import React,{useEffect,useState} from "react";
import "./main-area.css";
import {Card,Row,Col} from "react-bootstrap"
import {useHistory} from 'react-router-dom'
import axios from "axios"

function Mainarea(props) {
    
  const [countrydata, setCountrydata] = useState(null)
    const history=useHistory()
    useEffect(() => {
      axios.get("https://corona.lmao.ninja/v2/countries/india").then((response)=>{
        setCountrydata(response.data)
      })
    }, [null])
    
  return (
    <div>
      <div className="main-area">
        <h1>Statistics</h1>
        <div className="btn-area">
          <button onClick={()=>{
              history.push('/')
          }} className={`country-btn ${props.value == "my-country" ? "btnz-active":"" }`} >My Country</button>

          <button onClick={()=>{
              history.push('/global')
          }} className={`global-btn ${props.value == "global" ? "btnz-active" : ""} `} >Global</button>
        </div>
        <div className="display-area col-xs-12">

    <Row xs={2} md={4} className="g-4">
  
    <Col>
      <Card className="cards-main Affected">
        <Card.Body>
        <h5>Affected</h5>
         <h2>{countrydata ? countrydata.cases : "Check Update"}</h2>
        </Card.Body>
      </Card>
    </Col>

    <Col>
      <Card className="cards-main Death">
        <Card.Body>
        <h5>Death</h5>
         <h2>{countrydata ? countrydata.deaths : "Check Update"}</h2>
        </Card.Body>
      </Card>
    </Col>

    <Col>
      <Card className="cards-main recover ">
        <Card.Body>
        <h5>Recovered</h5>
         <h2>{countrydata ? countrydata.recovered : "Check Update"}</h2>
        </Card.Body>
      </Card>
    </Col>
    <Col>

      <Card className="cards-main active ">
        <Card.Body>
        <h5>Active</h5>
         <h2>{countrydata ? countrydata.active : "Check Update"}</h2>
        </Card.Body>
      </Card>
    </Col>
  
</Row>
          
        </div>

        

      </div>
    </div>
  );
}

export default Mainarea;
